export function formatUringWeightTimer(weightList, pickerDate, interval) {
    function formatStartTime(interval, endTime) {
        if (interval === 0) {
            return endTime - (24 * 60 * 60 * 1000)
        }
        if (interval === 0) {
            return endTime - (3 * 24 * 60 * 60 * 1000)
        }
        if (interval === 0) {
            return endTime - (7 * 24 * 60 * 60 * 1000)
        }
        return endTime - (14 * 24 * 60 * 60 * 1000)
    }
    const endTime = pickerDate + (31 * 60 * 60 * 1000)
    const startTime = formatStartTime(interval, endTime)
    
}

export function FeedData(QDC) {
    //進食
    if (Array.isArray(QDC)) {
        const filter = QDC.filter(function (info, index, arr) {
            return info.item.toString().trim() === "進食"
        })
        return filter
    }
    return false
}

export function IVData(QDC) {
    //血品&輸液 line:IV
    if (Array.isArray(QDC)) {
        const filter = QDC.filter(function (info, index, arr) {
            return info.item.toString().trim() === "血品" || info.item.toString().trim() === "輸液"
        })
        return filter
    }
    return false
}

export function UrineData(QDC) {
    //排尿
    if (Array.isArray(QDC)) {
        const filter = QDC.filter(function (info, index, arr) {
            return info.item.toString().trim() === "排尿"
        })
        return filter
    }
    return false
}

export function OtherData(QDC) {
    //第四區不屬於排尿
    if (Array.isArray(QDC)) {
        const filter = QDC.filter(function (info, index, arr) {
            return info.item.toString().trim() === "失血" ||
                info.item.toString().trim() === "滲液" ||
                info.item.toString().trim() === "嘔吐" ||
                info.item.toString().trim() === "引流" ||
                info.item.toString().trim() === "穿刺" ||
                info.item.toString().trim() === "透析" ||
                info.item.toString().trim() === "沖洗" ||
                info.item.toString().trim() === "排便"
        })
        return filter
    }
    return false
}