import React, { Component } from "react";
import { Labwarningvalue } from "../../Function/Wardstate";
import moment from "moment";
import { Tooltip } from "antd";

class Labviewitem extends Component {
  TimeRender(time) {
    return (
      <centerstyle>
        <Tooltip placement="bottom" title="06:30">
          {moment(time, "YYYY-MM-DD").format("MM/DD")}
        </Tooltip>
        <t3 style={{ color: "rgba(0,0,0,0.4)" }}></t3>
      </centerstyle>
    );
  }

  fixtitle = title => {
    switch (title) {
      case "Seg":
      case "Lymp":
        return (
          <div>
            <t3>{title}</t3>
            <t3 style={{ fontSize: "10px" }}>%</t3>
          </div>
        );
      case "Na":
      case "K":
      case "Ca++":
        return (
          <div>
            <t3>{title}</t3>
            <sup>++</sup>
          </div>
        );
      case "Mg":
        return (
          <div>
            <t3>{title}</t3>
            <sup>2+</sup>
          </div>
        );
      case "PCO":
      case "PO":
        return (
          <div>
            <t3>{title}</t3>
            <sub>2</sub>
          </div>
        );
      default:
        break;
    }
    return title;
  };

  render() {
    function formatValue(value, title) {
      if (title === "FeSat") {
        if (value === "--") {
          return "--"
        }
        return value + "%"
      }
      return value
    }
    function RowsGrid(source) {
      if (source === "Wardview") {
        return "1fr 1fr 1fr";
      }
      return "1fr 1fr 1fr";
    }
    function RowsHeight(source) {
      if (source === "Wardview") {
        return "80px";
      }
      return "80px";
    }
    const { value, title, time, Source } = this.props;
    const TimeR = this.TimeRender(time);
    const fixtitle = this.fixtitle(title);
    return (
      <div style={{
          display: "grid",
          gridTemplateRows: RowsGrid(Source),
          height: RowsHeight(Source)
        }}>
        <centerstyle>
          {/* <t3>{title}</t3> */}
          {fixtitle}
        </centerstyle>
        <centerstyle>
          <t2
            style={{
              fontSize: "20px",
              color: Labwarningvalue(value, title) ? "black" : "red"
            }}
          >
            {formatValue(value, title)}
          </t2>
        </centerstyle>
        {value == "--" || TimeR}
      </div>
    );
  }
}

export default Labviewitem;
